@charset "utf-8";

body,td,th,p{
	font-family: Arial, Helvetica, sans-serif;
	font-size: 10pt;
	color: #333;
	line-height: 1.5;
}
body {
	background-color: #fdfdfd;
	margin: 0;
	position:relative;
}
ul, li {
	font-family: Arial, Helvetica, sans-serif;
	font-size: 1em;
	color: #805215;
}
a {
	color: orange;
	text-decoration: none;
}
a:hover, a:focus {
	color: #3397db;
	text-decoration: none;
}
a img{
	border: none 0px #fff;
}
h1, h2, h3, h4, h5, h6 {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0 0 20px;
}
article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block;
}
#maincontent {
  background-color: #f3f3f3;
  min-height: 100%;
}
#footer {
  background-color: #444;
  color: #fff;
  font-size: 8pt;
  letter-spacing: 1px;
  padding: 25px;
  text-align: center;
  text-transform: uppercase;
}
nav {
  width: 100%;
  height: 20%;
  background-color: #252831;
  text-align:center;
}
nav h1 {
  margin: auto;
}
nav h1 a {
  color: #fff;
  font-size: 14pt;
  font-weight: 200;
  letter-spacing: 10px;
  text-transform: uppercase;
}
#breadcrumb {
    padding: 10px 40px 16px;
    list-style: none;
    background-color: #eee;
    font-size: 17px;
    margin: 0;
    padding: 0.5rem;
    width: 100%;
    flex: 1;
}

#breadcrumb li {
    top: 0;
    left: 0;
    display: inline;
}

#breadcrumb li+li:before {
    padding: 8px;
    color: black;
    content: "/\00a0";
}

#breadcrumb li a {
    color: #064479;
    text-decoration: none;
}

#breadcrumb li a:hover {
    color: #01447e;
    text-decoration: underline;
}
/* ====================== Map ====================== */
#map {
  height: 400px;
  width: 100%;
  background-color: #ccc;
}
#map-restaurant {
  height: 400px;
  width: 100%;
  background-color: #ccc;
}
/* ====================== Restaurant Filtering ====================== */
.filter-options {
  width: 100%;
  height: 100%;
  background-color: #3397DB;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}
.filter-options h2 {
  color: #000;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1;
  margin: 10px 15px;
  min-width: 100px;
}
.filter-options select {
  background-color: white;
  border: 1px solid #fff;
  font-family: Arial,sans-serif;
  font-size: 11pt;
  height: 35px;
  letter-spacing: 0;
  margin: 10px;
  padding: 0 10px;
  max-width: 320px;
  min-width: 300px;
  flex: 1;
}

/* ====================== Restaurant Listing ====================== */
#restaurants-list {
  background-color: #f3f3f3;
  list-style: outside none none;
  margin: 0;
  padding: 30px 15px 60px;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}
#restaurants-list li {
  background-color: #fff;
  border: 2px solid #ccc;
  font-family: Arial,sans-serif;
  margin: 15px auto;
  min-height: 380px;
  padding: 0 30px 25px;
  text-align: left;
  width: 270px;
  float: 1;
}
#restaurants-list .restaurant-img {
  background-color: #ccc;
  display: block;
  margin: 0;
  max-width: 100%;
  /*min-height: 248px;*/
  min-width: 100%;
}
#restaurants-list li h1 {
  /*color: #f18700;*/
  color: #805215;
  /*background-color: #ccc; */
  font-family: Arial,sans-serif;
  font-size: 14pt;
  font-weight: 600;
  letter-spacing: 0;
  line-height: 1.3;
  margin: 20px 0 10px;
  text-transform: uppercase;
}
#restaurants-list p {
  margin: 0;
  font-size: 11pt;
}
#restaurants-list li a {
  background-color: orange;
  border-bottom: 3px solid #000;
  color: #000;
  display: inline-block;
  font-size: 10pt;
  margin: 15px 0 0;
  padding: 8px 30px 10px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}

/* ====================== Restaurant Details ====================== */
.inside {
  display: flex;
}
.inside header {
  position: fixed;
  right: 0;
  width: 100%;
  z-index: 1000;
}
@media(min-width: 900px) {
  .inside #map-container {
    background: blue none repeat scroll 0 0;
    height: 79%;
    position: fixed;
    right: 0;
    top: 12%;
    margin: 0px 15px;
    width: 50%;
    flex: 0 0 12em;
  }
  #restaurant-container, #reviews-container {
    border-bottom: 1px solid #d9d9d9;
    border-top: 1px solid #fff;
    padding: 140px 5% 30px;
    width: 40%;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}
@media(max-width: 900px) {
  .inside {
    display: flex;
  }
  .inside #map-container {
    background: blue none repeat scroll 0 0;
    height: 400px;
    padding: 70 5 5;
    margin: 5px;
    order: 3;
    flex: 1;
  }
  #restaurant-container,
  #reviews-container {
    margin: auto;
    /*padding: 30px auto 80px;*/
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  #restaurant-container {
    flex: 1;
    order: 1
  }
  #reviews-container {
    flex: 1;
    order: 2
  }
  .filter-options h2 {
    flex: 1;
  }
}
@media(max-width: 768px) {
  .filter-options h2 {
    min-width: 100%;
    flex: 1;
  }
  .inside #map-container {
    background: blue none repeat scroll 0 0;
    height: 400px;
    padding: 70 5 5;
    margin: 5px;
    order: 3;
    flex: 1;
  }
  .inside #map-restaurant {
    background-color: #ccc;
    height: 400px;
    width: 100%;
  }
}
.inside #map-restaurant {
  background-color: #ccc;
  height: 100%;
  width: 100%;
}
.inside #footer {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
  text-align: center;
  flex: 1;
}
#restaurant-name {
  color: #805215;
  font-family: Arial,sans-serif;
  font-size: 20pt;
  font-weight: 200;
  letter-spacing: 0;
  margin: 15px 0 30px;
  text-transform: uppercase;
  line-height: 1.1;
  width: 100%;
  text-align: center;
}
#restaurant-img {
	width: 80%;
  height: auto;
  margin: 5%;
}
#restaurant-address {
  font-size: 12pt;
  margin: 10px 0px;
}
#restaurant-cuisine {
  background-color: #333;
  color: #ddd;
  font-size: 12pt;
  font-weight: 300;
  letter-spacing: 10px;
  margin: 0 0 20px;
  padding: 2px 0;
  text-align: center;
  text-transform: uppercase;
	width: 90%;
}
/*#restaurant-container, #reviews-container {
  flex: 1;
}*/

#reviews-container h2 {
  color: #805215;
  font-size: 24pt;
  font-weight: 300;
  letter-spacing: -1px;
  padding-bottom: 1pt;
  width: 100%;
  text-align: center;
}
#reviews-list {
  margin: 0 5%;
  padding: 0 0 50px;
}
#reviews-list li {
  background-color: #fff;
    border: 2px solid #f3f3f3;
  display: block;
  list-style-type: none;
  margin: 0 0 30px;
  overflow: hidden;
  padding: 0 20px 20px;
  position: relative;
  width: 85%;
}
#reviews-list li p {
  margin: 0 0 10px;
}
#restaurant-hours td {
  color: #666;
}
#restaurants-scroll {
  height: 564px;
  overflow: scroll;
  border: 1px dotted #F00;
}
